/* src/App.css */

.App {
  overflow-x: hidden;
  max-width: 100vw;
}

/* global styles */
body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

header, section, footer {
  padding: 0 20px;
}

/* Media Queries */

/* Small devices (landscape phones, 768px and up) */
@media (max-width: 768px) {
  header, section, footer {
    padding: 0 10px;
  }
}

/* Extra small devices (portrait phones, 576px and up) */
@media (max-width: 576px) {
  header, section, footer {
    padding: 0 5px;
  }
}
