/* src/components/Header/Header.css */

/* Importing variables.css */
@import "../../../src/Assets/variables.css";
@import "../../../src/Assets/fonts/fonts.css";

.header {
  background-color: var(--primary-color);
  padding: 20px 0;
  width: 100%;
  height: 90vh;
  margin-bottom: 50px;
  overflow-x: hidden; /* Prevent horizontal overflow */
}

.nav-container {
  display: flex;
  justify-content: space-between;
  max-width: 1600px;
  margin-top: 5px;
}

.logo {
  margin-left: 200px;
  height: 5%;
  width: 7%;
}

.nav-container ul {
  margin-top: 50px;
  list-style: none;
  display: flex;
}

.nav-container li {
  margin-left: 20px;
}

.nav-container a {
  font-family: SFPro, sans-serif;
  color: var(--text-color);
  text-decoration: none;
  font-size: 1.3em;
  font-weight: 200;
  transition: color 0.3s ease;
}

.nav-container ul::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  background-color: var(--secondary-color);
  bottom: -5px;
  left: 0;
  transform: scaleX(0);
  transition: transform 0.3s ease;
}

.nav-container ul:hover::after {
  transform: scaleX(1);
}

/* Hero section */
.hero {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  margin-top: 60px;
}

.hero span {
  font-size: 2.5em;
}

.hero-text-small {
  font-family: "Exo", sans-serif;
  font-size: 1.3em;
}

.hero-text {
  font-family: "MediaSans", sans-serif;
  font-size: 12em;
  justify-content: center;
  align-items: center;
  margin-top: 1.5px;
  color: var(--text-color);
  font-weight: 800;
  letter-spacing: 2px;
}

.social-icons {
  margin-top: -100px;
  text-align: center;
}

.social-icons a {
  margin: 0 10px;
  font-size: 2em;
  color: #333;
  transition: color 0.3s;
}

.social-icons a:hover {
  cursor: pointer;
}

.button-container {
  display: flex;
  justify-content: center;
  margin-top: 70px;
}

.primary {
  font-family: SFPro, sans-serif;
  padding: 10px 20px;
  font-size: 1.5em;
  letter-spacing: 1px;
  color: var(--primary-color);
  border: none;
  border-radius: 4px;
  cursor: pointer;
  background-color: var(--text-color);
  transition: background-color 0.3s ease;
}

.primary:hover {
  cursor: pointer;
}

.secondary {
  font-family: SFPro, sans-serif;
  padding: 10px 20px;
  font-size: 1.5em;
  letter-spacing: 1px;
  color: var(--text-color);
  border: 2px solid var(--text-color);
  border-radius: 4px;
  cursor: pointer;
  margin-left: 10px;
  background-color: var(--primary-color);
  transition: color 0.3s ease;
}

.secondary:hover {
  background-color: var(--text-color);
  color: var(--primary-color);
}

/* Hamburger menu styles */
.hamburger {
  display: none;
  flex-direction: column;
  cursor: pointer;
}

.hamburger div {
  width: 25px;
  height: 3px;
  background-color: #000;
  margin: 4px 0;
}

/* Collapsed navigation menu */
.nav-container ul {
  flex-direction: row;
}

.nav-container ul.collapse {
  display: none;
}

/* Media Queries */
/* Small devices (landscape phones, 768px and up) */
@media (max-width: 768px) {
  .header {
    max-width: 100%;
  }

  .nav-container {
    margin-top: 40px;
    margin-right: 40px;
  }

  .logo {
    margin-left: 50px;
    height: 10%;
    width: 13%;
  }

  .nav-container ul {
    display: none;
    flex-direction: column;
    width: 100%;
    background-color: white;
    color: white;
    position: absolute;
    height: 100vh;
    top: 0;
    left: 0;
  }

  .nav-container ul.collapse {
    display: flex;
  }

  .hamburger {
    display: flex;
  }

  .social-icons {
    margin-top: 0px;
    text-align: center;
  }

  .hero {
    margin-top: 100px;
    display: flex;
    flex-direction: column;
  }

  .hero-text {
    margin-top: 20px;
    font-size: 4em;
  }

  .primary {
    padding: 10px 20px;
    font-size: 1.2em;
}

  .secondary {
    padding: 10px 20px;
    font-size: 1.2em;
  }

}

/* Extra small devices (portrait phones, 576px and up) */
@media (max-width: 576px) {
  .logo {
    height: 40px;
  }

  .nav-container ul {
    font-size: var(--font-size-small);
  }

  .hero-text-small {
    font-size: 1em; /* Adjust as needed */
  }

  .hero-text {
    font-size: 3em; /* Adjust the font size */
  }
}
